#main_header{
    height: 100px;
    background: #fff;
    border-bottom: 1px #134f2c solid;
    nav{
        position: relative;
        z-index: 9999;
        height: 100%;
        display: flex;
        justify-content: space-around;
        h1{ 
            width: 150px;
            background: url("../public/img/logo.png") no-repeat center;
            background-size: contain;
            cursor: pointer;
            text-indent: -9999px;
        }
        .gnb{
            display: flex;
            a{
                font-weight: bold;
            }
            &>li{
                &>a{
                    display: block;
                    height: 100%;
                    line-height: 100px;
                    padding: 0px 50px;
                    color: #333;
                }
                &:hover{
                    &>a{
                        background: #134f2c;
                        color: #fff;
                    }
                }
            }
            .sub{
                width: 100%;
                height: 350px;
                display: none;
                a{
                    display: block;
                    height: 100%;
                    padding: 10px 0px;
                    color: #888;
                    &:hover{
                        color: #0055ff;
                    }
                }
            }
            &:hover+.gnb_bg{
                display: block;
            }
            &:hover{
                .sub{
                    display: block;
                }
            }
        }
        .gnb_bg{
            display: none;
            width: 100%;
            height: 350px;
            position: absolute;
            z-index: -1;
            top: 100px;
            left: 0px;
            background: #fff;
            border-top: 1px #134f2c solid;
            border-bottom: 1px #134f2c solid;
        }
    }
    
}
.sub_bg{
    width: 100%;
    height: 300px;
    background: #acacac;
}
#Company{
    .cpy{
        background: url("../public/img/cpybg.jpg") no-repeat;
    }
    .jpm{
        background: url("../public/img/jpmbg.jpg") no-repeat;
    }
    h2{ 
        text-align: center;
        font-size: 30px;
        padding: 50px 0px;
        margin: 0px 200px;
        border-bottom: 1px #ccc solid;
        color:#134f2c
    }
    .CompanyList{
        padding-top: 50px;
        margin: 0px 200px;
        border-bottom: 1px #ccc solid;
        ul{
            display: flex;
            border-bottom: 1px #ccc solid;
            li{
                button{
                    padding: 20px;
                    border: 1px #ccc solid;
                    border-bottom: 0px;
                    border-left: 0px;
                    background: #fff;
                    
                }
                &:first-child{
                    button{
                        border-left: 1px #ccc solid;
                    }
                }
                button.list{
                    background: #555;
                    color: #fff;
                }
            }
        }
    }
    .CEO{
        margin: 100px 200px;
        margin-top: 50px;
        height: 500px;
        display: flex;
        justify-content: flex-start;
        &>div{
            width: 45%;
            height: 100%;
        }
        .company_img{
            position: relative;
            img{
                position: absolute;
                left: 0px;
                top: 0px;
            }
        }
        .company_txt{
            h3{
                font-size: 30px;
                margin-bottom: 50px;
            }
            p{  
                font-size: 14px;
                line-height: 40px;
                word-break: keep-all;
                color: #555;
                font-weight: 700;
                strong{
                    font-size: 18px;
                    color: #134f2c;
                    font-weight: bold;
                }
            }
            h4{
                font-size: 20px;
                position: relative;
                margin-top: 50px;
                &::after{
                    content: "";
                    position: absolute;
                    display: block;
                    transform: translateY(-50%);
                    top: 50%;
                    left: 150px;
                    width: 100px;
                    height: 50px;
                    padding-left: 10px;
                    background: #ccc;
                }
            }
        }
    }
    #map{
        margin: 0px 200px;
        height: 500px;
        background: #ccc;
    }
    .map_box{
        margin: 50px 200px;
        li{
            display: flex;
           padding: 15px 0px;
            border-top: 1px #ccc solid;
            &:last-child{
                border-bottom: 1px #ccc solid;
            }
        }
        p{  
            padding-left: 50px;
            height: 50px;
            line-height: 50px;
            font-size: 20px;
            font-weight: bold;
        }
        span{
            width: 50px;
            display: block;
            height: 50px;

        }
        .location{
            background: url("../public/img/location.jpg") no-repeat center;
        }
        .bus{
            background: url("../public/img/bus.jpg") no-repeat center;
        }
        .subway{
            background: url("../public/img/subway.jpg") no-repeat center;
        }

    }
    .LM{
        margin: 50px 200px;
        padding-bottom: 50px;
        border-bottom: 1px #ccc solid;
        .LM_list{
            display: flex;
            justify-content: space-between;
            gap: 50px;
            li{
                cursor: pointer;
                flex: 1 1 0;
                div{
                    width: 100%;
                    height: 100%;
                    overflow: hidden;
                    border: 1px #ccc solid;
                    box-sizing: border-box;
                }
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                    transition: all .4s;
                    &:hover{
                        scale: 1.1;
                    }
                }
                figcaption{
                    padding-top: 10px;
                    text-align: center;
                }
            }
        }
        
    }
    .lms{
        margin: 0 200px;
        ul{
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 50px;
            row-gap: 50px;
            li{
                border: 1px #ccc solid;
                height: 300px;
                cursor: pointer;
                &:hover img{
                    scale: 1.1;
                }
                figure{
                    width: 100%;
                    height: 100%;
                    div{
                        width: 100%;
                        height: 240px;
                        overflow: hidden;
                    }
                }
                figcaption{
                    width: 100%;
                    margin-top: 20px;
                    font-weight: bold;
                    font-size: 14px;
                    text-align: center;
                }
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                    transition: all .4s;

                }
            }
        }
    }
}
#main_footer{
    width:100%;
    margin-top: 50px;
    border-top: 1px #ccc solid;
    .footer_top{
        height: 150px;
        border-bottom: 1px #ccc solid;;
        display: flex;
        &>div{
            width: 500px;
            padding-top: 20px;
            padding-left: 200px;
        }
        &>div:first-child{
            li{
                display: flex;
                p::before{
                    content: "|";
                    padding: 0px 10px;
                    color: #555;
                }
                p:first-child::before{
                    display: none;
                }
            }
        }
        span{
            color: #000;
        }
        h2{ margin-bottom: 10px; }
        p{ 
            margin-bottom: 10px;
            color: #555;
        }
        address{
            span{
                display: inline-block;
                color: #000;
                text-align: center;
                width: 100px;
            }
        }
    }
    .copyright{
        width: 100%;
        height: 50px;
        line-height: 50px;
        background: #0055ff;
        color: #fff;
        text-align: center;
    }
}

