body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body,header,section,nav,footer,div,h1,h2,h3,h4,ul,ol,li,p,address,button,figure,figcaption,table,thead,tbody,tfoot,caption,tr,th,td{
  margin: 0px;
  padding: 0px;
}
ul{
  list-style: none;
}
a{ text-decoration: none;}
button{
  display: block;
  cursor: pointer;
  border: 0;
}
address{
  display: block;
  font-style: normal;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
